<template>
  <div class="login-page">
    <div v-if="succeed" class="alert alert-success alert-link">
      {{ $t("userRegisteredPleaseLogIn") }}
    </div>
    <div v-if="problem" class="alert alert-danger alert-link">
      {{ $t("wrongUser") }}
    </div>
    <div class="form">
      <form v-if="showRegister" class="register-form">
        <input v-model="username" type="text" :placeholder="$t('username')" />
        <input v-model="password" type="password" :placeholder="$t('password')" />
        <input v-model="emailAddress" type="text" :placeholder="$t('emailAddress')" />
        <button @click.prevent="register()">{{ $t("register") }}</button>
        <p class="message">
          {{ $t("alreadyRegistered?") }}
          <a href="#" @click.prevent="showRegister = false">{{ $t("login") }}</a>
        </p>
      </form>
      <form v-else class="login-form">
        <input v-model="username" type="text" :placeholder="$t('username')" />
        <input v-model="password" type="password" :placeholder="$t('password')" />
        <button @click.prevent="login()">{{ $t("login") }}</button>
        <p class="message">
          {{ $t("notRegistered?") }}
          <a href="#" @click.prevent="showRegister = true">{{ $t("register") }}</a>
        </p>
      </form>
    </div>
  </div>
</template>

<script>
import UsersService from "../services/UsersService";

export default {
  name: "login",
  data() {
    return {
      showRegister: false,
      username: "",
      password: "",
      email: "",
      succeed: false,
      problem: false,
    };
  },
  methods: {
    async login() {
      try {
        let result = await UsersService.signIn(this.username, this.password);
        let resultData = result.data;
        if (resultData.hasSucceed) {
          this.problem = false;
          UsersService.addLoginToLocalStorage(resultData.responseObject._id);
          window.location.reload();
          this.$router.push("/");
        } else {
          this.succeed = false;
          this.problem = true;
        }
      } catch (err) {
        this.error = err.message;
        console.error(err);
      }
    },
    async register() {
      try {
        let result = await UsersService.registerUser(
          this.username,
          this.password,
          this.email
        );
        let resultData = result;
        if (resultData.hasSucceed) {
          this.problem = false;
          this.succeed = true;
          this.showRegister = false;
          this.username = "";
          this.password = "";
        } else {
          this.succeed = false;
          this.problem = true;
        }
      } catch (err) {
        this.error = err.message;
        console.error(err);
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.login-page {
  width: 360px;
  padding: 10% 0 0;
  margin: auto;
}

.form {
  position: relative;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.6);
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  border-radius: 15px;
  text-align: center;
}

.form input {
  font-family: "Roboto", sans-serif;
  outline: 1;
  background: #f2f2f2;
  width: 100%;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}

.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: cadetblue;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  cursor: pointer;
}

.form button:hover,
.form button:active {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), cadetblue;
}

.form .message {
  margin: 15px 0 0;
  font-size: 12px;
}

.form .message a {
  color: cadetblue;
  text-decoration: none;
}
</style>
